import { reactive } from 'vue';
import useVuelidate from '@vuelidate/core';
import {
  required,
  email,
  minLength,
  maxLength
} from '@vuelidate/validators';

export const useRegisterForm = () => {

  const form = reactive({
    firstName: '',
    lastName: '',
    email: '',
    password: '',
    language: 'pt_BR',
    emailNewsletters: true
  });

  const rules = {
    firstName: {
      required,
      minLength: minLength(3),
      maxLength: maxLength(30)
    },
    lastName: { required, minLength: minLength(3), maxLength: maxLength(50) },
    email: { 
      required, 
      email           
    },
    password: { required, minLength: minLength(8) },
    language: { required },
    emailNewsletters: { required }
  };

  const model = useVuelidate(rules, form);

  const onSubmit = async () => {
    if (await model.value.$validate()) {
      console.log('submit...');
    }
    /*
    model.value.$touch();
    if (model.value.$invalid) return;
    alert('Form Submitted ' + JSON.stringify(form, null, 2));
    */
  };

  return {
    onSubmit,
    model
  };
};
