<template>
  <div v-if="error && !loading">
    {{ error }}
  </div>

  <div v-if="!error && loading">
    loading...
  </div>
  <div v-if="!error && !loading" class="card-group">
    <div class="card">
      <h2>Register</h2>

      <form @submit.prevent="onSubmit" novalidate class="form-group">
        <Input
          name="firstName"
          label="First Name"
          placeHolder="Your first name"
          :field="model.firstName"
        />

        <Input
          name="lastName"
          label="Last Name"
          placeHolder="Your last name"
          :field="model.lastName"
        />

        <Input
          name="email"
          label="E-mail"
          placeHolder="your@email"
          :field="model.email"
        />

        <Input
          name="password"
          label="Password"
          placeHolder="password"
          :field="model.password"
        />

        <Select
          name="language"
          label="Language"
          placeHolder="--select language--"
          :field="model.language"
          :list="languageList"
        />

        <Input
          name="emailNewsletters"
          label="Newsletters"
          placeHolder="newsletters...."
          :field="model.emailNewsletters"
        />

        <div class="button r" id="button-3">
          <input type="checkbox" class="checkbox" v-model="model.emailNewsletters.$model">
          <div class="knobs"></div>
          <div class="layer"></div>
        </div>

        <div>
          <button :disabled="model.$error" class="success">
            Register
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import services from '@/http';

import { useFieldHandlers } from '@/modules/app/composables/useFieldHandlers';
import { useRegisterForm } from '../composables/useRegisterForm';
import Input from '@/modules/app/components/vuelidate/Input';
import Select from '@/modules/app/components/vuelidate/Select';

export default {
  name: 'Register',
  components: { Input, Select },
  data() {
    return {
      loading: true,
      error: null,
      languageList:[]
    };
  },
  async created() {
    await services.app.findAllLanguages()
      .then(res => {
        let list = [];
        // converting objects to Select component format.
        res.data.languages.map(i => {
          list.push({ key:i.key, name:i.language });
        });
        this.languageList = list;
      })
      .catch(err => {
        this.error = err.message;
      })
      .finally(() =>{
        this.loading = false;
      })
  },
  setup() {
    const { handleUpdate } = useFieldHandlers();
    const { model, onSubmit } = useRegisterForm();
    return {
      model,
      handleUpdate,
      onSubmit
    };
  }
};
</script>
<style lang="scss">
@import '../../../assets/scss/abstract/variables';
</style>
