<template>
  <div class="card-group">
    <div class="card">
      <h2>Login</h2>

      <form @submit.prevent="onSubmit" class="form-group">
        <div>
          <label htmlFor="email">Email:</label>
          <input
            type="text"
            name="email"
            v-model.trim="form.email"
            @blur="handleBlur(model.email, $event)"
            placeholder="Informe seu e-mail"
            :class="{ 'is-invalid': !isValid('email') }"
          />
          <span v-if="model.email.required.$invalid">
            *Campo obrigatório.
          </span>
          <span v-if="model.email.email.$invalid">
            *Email inválido.
          </span>
          <ErrorMessage :field="model.email" />
        </div>
        <div>
          <label htmlFor="password">Senha:</label>
          <input
            type="password"
            name="password"
            v-model.trim="form.password"
            @blur="handleBlur(model.password, $event)"
            placeholder="Informe sua senha"
            :class="{ 'is-invalid': !isValid('password') }"
          />
          <ErrorMessage :field="model.password" />
        </div>

        <div>
          <button
            :disabled="model.$invalid"
            class="success"
            :class="{ loading: userLoading }"
          >
            Login
          </button>
        </div>

        <div>
          <ul>
            <li>
              <router-link class="link" :to="{ name: 'forgot-password' }"
                >Forgot password</router-link
              >
            </li>
            <li>
              <router-link class="link" :to="{ name: 'register' }"
                >Register</router-link
              >
            </li>
          </ul>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import { useLoginForm } from '../composables/useLoginForm';
import ErrorMessage from '@/modules/app/components/vuelidate/ErrorMessage.vue';

export default {
  name: 'Login',
  components: { ErrorMessage },
  setup() {
    const { form, model, handleBlur, isValid, onSubmit } = useLoginForm();
    return {
      form,
      model,
      handleBlur,
      isValid,
      onSubmit
    };
  },
  computed: {
    ...mapState('user', ['userLoading'])
  }
};
</script>
<style lang="scss">
@import '@/assets/scss/abstract';

.is-valid {
  background: get-color(success);
}

.is-invalid {
  background: get-color(danger);
}
</style>
