import { reactive } from 'vue';
import useVuelidate from '@vuelidate/core';
import { required, email, minLength } from '@vuelidate/validators';

import store from '@/store';
import router from '@/router';

export const useLoginForm = () => {
  const form = reactive({
    email: 'clau.li.erd@gmail.com',
    password: '12345678'
  });

  const rules = {
    email: { required, email },
    password: { required, minLength: minLength(8) }
  };

  const model = useVuelidate(rules, form);

  const handleBlur = (key, event) => {
    //model.value[key].$dirty = true;

    //if (model.value[key].$error) {
    if (key.$error) {
      event.target.classList.add('is-invalid-shake');
      setTimeout(() => {
        event.target.classList.remove('is-invalid-shake');
      }, 600);
    }
  };

  const isValid = key => {
    return !model.value[key].$error;
  };

  const onSubmit = async () => {
    /*
    if (await model.value.$validate()) {
    }
    */

    model.value.$touch();
    if (model.value.$invalid) return;

    store
      .dispatch('user/ActionLogin', form)
      .then(() => {
        let redirect = router?.currentRoute?.value?.query?.r;
        if (!router.hasRoute(redirect)) {
          redirect = 'account';
        }
        router.push({ name: redirect });
      })
      .catch(err => {
        console.log(err.error);
      });

    //alert("Form Submitted " + JSON.stringify(form, null, 2));
  };

  return {
    form,
    model,
    handleBlur,
    isValid,
    onSubmit
  };

  /*
  const userEmail = ref('');
  const userPassword = ref('');
  const rules = {
    userEmail: { required, email },
    userPassword: { required, minLength: minLength(8) }
  };
  const v = useVuelidate(rules, { userEmail, userPassword });
  const handleBlur = key => {
    v.value[key].$dirty = true;
  };


  const login = () => {
    v.$dirty = true;
    if (!v.$error) {
      // DO SOMETHING
      console.log("do login...");
    }
  };
  return {
    userEmail,
    userPassword,
    v,
    handleBlur,
    login
  };

  */
};
