<template>
  <div>
    <h1>Logout</h1>
  </div>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  name: 'Logout',
  async created() {
    await this.ActionLogout();
    this.$router.push({ name: 'home' });
  },
  methods: {
    ...mapActions('user', ['ActionLogout'])
  }
};
</script>

