export const useFieldHandlers = () => {

	const handleInput = (key, event) => {
		event.target.classList.remove('field-is-valid');
    	key.$model = event.target.value;
    	key.$reset();
	};	

	const handleBlur = async (key, event) => {
		await key.$touch();
	    if (key.$error) {
	      event.target.classList.add('field-is-invalid-shake');
	      setTimeout(() => {
	        event.target.classList.remove('field-is-invalid-shake');
	      }, 600);
	    } else {
	    	event.target.classList.add('field-is-valid');
	    }
	};

	return {
		handleInput,
		handleBlur
	}

}