<template>
  <div>
    <label :htmlFor="name">{{ label }}: {{ field.$model }}</label>
    <select
      :name="name"
      @blur="handleBlur(field, $event)"
      @input="handleInput(field, $event)"
      :class="{ 'field-is-invalid': field.$error }"
    >
      <option value="">{{ placeHolder }}</option>
      <option :value="l.key" :selected="l.key===field.$model" :key="l.key" v-for="l in list">
        {{ l.name }}
      </option>
    </select>
    <ErrorMessage :field="field" />
  </div>
</template>
<script>
import { useFieldHandlers } from '../../composables/useFieldHandlers';
import ErrorMessage from './ErrorMessage';

export default {
  name: 'Select',
  components: { ErrorMessage },
  data: () => ({
    valor: ''
  }),
  props: {
    name: {
      type: String,
      required: true
    },
    placeHolder: {
      type: String,
      required: true
    },
    label: {
      type: String,
      required: true
    },
    field: {
      type: Object,
      required: true
    },
    list: {
      type: Array,
      required: true
    }
  },
  emits: ['update'],
  setup(props, { emit }) {
    const { handleInput, handleBlur } = useFieldHandlers();
    const updateInput = value => {
      emit('update', value);
    };
    return { updateInput, handleInput, handleBlur };
  }
};
</script>
<style lang="scss">
@import '@/assets/scss/abstract';
</style>
